body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #2a3132;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* main infowindow div
height and width for the info window are controlled here,
height must match custom infowindow height  */
 
.gm-style-iw .gm-style-iw-c, .gm-style .gm-style-iw-c{
  background: transparent  !important; /* Fallback color  */
  background: transparent !important;
  background-color:transparent !important;
}
.gm-style-iw-d{
  background: rgb(0,0,0)  !important; /* Fallback color  */
  background: rgba(0,0,0, 0.4) !important;

}


.gm-style-iw {
  margin:0;
  padding: 0;
  min-width: 300px; 
  min-height: 200px;
  box-shadow: 10px 10px 10px #2a3132 !important;
}

.gm-style .gm-style-iw{
  padding:0;

}
 .gm-style-iw-d{
  max-width: 100vw !important;
  max-height: 100vh !important;
}

.gm-style .gm-style-iw-d{
  overflow: hidden !important;

  
} 
/* tootip */
 .gm-style .gm-style-iw-t::after{
  background-color: rgb(0,0,0)  !important; 
  background-color: rgba(0,0,0, 0.4) !important;
} 
/* close */
 .gm-ui-hover-effect{
  width:40px !important;
  height: 40px !important;
  color:white !important;
}
.gm-ui-hover-effect img{
  width:25px !important;
  height: 25px !important;

} 



*,
*:before,
*:after{
  box-sizing: inherit;
}
* h1,* h2,* h3{
  font-family: 'Special Elite', cursive;
  /* justify-items: center;
  align-items: center; */
  text-align: center;
  padding: 5px 0 0;
  margin: auto;
}

body, html{
  height: 100%;
  scroll-behavior: smooth;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



